:root {
  --color-brand: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  text-transform: uppercase;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  line-height: 60px;
  text-decoration: none;
  list-style: none;
  display: block;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link:hover {
  color: var(--color-brand);
}

.is-active {
  color: var(--color-brand);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--color-brand);
  border-radius: 8px;
  width: 37px;
  height: 4px;
  position: absolute;
  top: 56px;
  bottom: 0;
  left: 0;
}

